import React, {Component} from 'react';
import {connect} from "react-redux";
import {createNewUser} from "../actions/securityActions";
import SignUpHook from "../hooks/SignUpHook";

class Register extends Component {

    constructor() {
        super();

        this.state = {
            "username": "",
            "fullname": "",
            "password": "",
            "confirmPassword": "",
            "errors": {}
        };

        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentDidMount() {
        if(this.props.security.validToken) {
            this.props.history.push("/dashboard");
        }
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors) {
            this.setState({errors: nextProps.errors})
        }
    }

    onSubmit(e) {
        e.preventDefault();
        const newUser = {
            "username": this.state.username,
            "fullname": this.state.fullname,
            "password": this.state.password,
            "confirmPassword": this.state.confirmPassword
        }

        this.props.createNewUser(newUser, this.props.history);
    }

    onChange(e) {
        this.setState({[e.target.name] : e.target.value})
    }

    render() {
        return (
            <SignUpHook onSubmit={this.onSubmit} fullname={this.state.fullname} onChange={this.onChange} username={this.state.username} password={this.state.password} confirmPassword={this.state.confirmPassword} errors={this.state.errors}/>
        );
    }
}

const mapStateToProps = state => ({
    errors: state.errors,
    security: state.security
});
export default connect(mapStateToProps, {createNewUser})(Register);