import React from 'react';
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import makeStyles from "@material-ui/core/styles/makeStyles";
import Button from "@material-ui/core/Button";
import {Link} from "react-router-dom";
import CardActions from "@material-ui/core/CardActions";
import ButtonGroup from "@material-ui/core/ButtonGroup";
const useStyles = makeStyles(theme => ({
    high: {
        backgroundColor: '#FFD5D1',
        marginBottom: theme.spacing(2),
    },
    medium: {
        backgroundColor: '#E1F1FF',
        marginBottom: theme.spacing(2),
    },
    low: {
        backgroundColor: '#E7FFE1',
        marginBottom: theme.spacing(2),
    },
    title: {
        color: theme.palette.grey[900],
    },
    id: {
        color: theme.palette.grey[900],
    }


}))
function ProjectTaskHook(props) {
    const {project_task} = props;
    const classes = useStyles();
    let cardClass;
    let cardString;

    if(project_task.priority === 1){
        cardClass = classes.high;
        cardString = "HIGH";
    }
    if(project_task.priority === 2){
        cardClass = classes.medium;
        cardString = "MEDIUM";
    }
    if(project_task.priority === 3){
        cardClass = classes.low;
        cardString = "LOW";
    }

    return (
        <Card className={cardClass}>
            <CardActions>
                <Button variant="contained">
                    ID: {project_task.projectSequence}
                </Button>
                <Button variant="contained">
                    {cardString}
                </Button>
            </CardActions>
            <CardContent>
                <Typography variant="h4" className={classes.title} gutterBottom>
                    {project_task.acceptanceCriteria}
                </Typography>
                <ButtonGroup variant="contained"  aria-label="contained primary button group">
                    <Button color="primary" component={Link} to={`/updateProjectTask/${project_task.projectIdentifier}/${project_task.projectSequence}`}>
                        View/ Update
                    </Button>
                    <Button color="secondary" onClick = {props.onDeleteClick.bind(this,project_task.projectIdentifier,project_task.projectSequence)}>
                        Delete
                    </Button>
                </ButtonGroup>
            </CardContent>
        </Card>
    );
}

export default ProjectTaskHook;