import React, {Component} from 'react';
import ProjectTask from "./projectTasks/ProjectTask";
import ProjectBoardHook from "../../hooks/ProjectBoardHook";

class Backlog extends Component {
    render() {
        const { project_tasks } = this.props;
        const tasks = project_tasks.map(project_task => (
           <ProjectTask key = {project_task.id} project_task={project_task} history={this.props.history}/>
        ));

        let todoItems = [];
        let inProgressItems = [];
        let doneItems = [];

        for(let i=0;i< tasks.length;i++) {
            if(tasks[i].props.project_task.status === "TO_DO")
                todoItems.push(tasks[i]);

            if(tasks[i].props.project_task.status === "IN_PROGRESS")
                inProgressItems.push(tasks[i]);

            if(tasks[i].props.project_task.status === "DONE")
                doneItems.push(tasks[i]);

        }

        return (
            <ProjectBoardHook todoItems={todoItems} inProgressItems={inProgressItems} doneItems={doneItems} id={this.props.id}/>
        );
    }
}

export default Backlog;