import React from 'react';
import makeStyles from "@material-ui/core/styles/makeStyles";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Divider from "@material-ui/core/Divider";
import CardContent from "@material-ui/core/CardContent";
import Card from "@material-ui/core/Card";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import {Link} from "react-router-dom";

const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(10),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100%',

    },
    content: {
        display: "flex",
        justifyContent: "space-between",
    },
    tag: {
        fontSize: theme.spacing(2),
    },
    newpaper: {

        marginTop: theme.spacing(10),
        display: 'flex',
        flexWrap: "nowrap",
        flexDirection: 'column',
        alignItems: 'stretch',
        overflow: "scroll",
        maxHeight: theme.spacing(80),
    },
    signup: {
        fontSize: theme.spacing(5.4)
    },
    link: {
        fontSize: "1.2em",
        marginTop: theme.spacing(5),
        '&:hover': {
            color: '#FFFFFF',
            textDecoration: 'none',
        },
    },
    root: {
        minHeight: theme.spacing(10),
        padding: "2rem",
        paddingRight: "5rem",
        backgroundColor: theme.palette.grey["700"],
    },
    margin: {
        marginBottom: theme.spacing(2),
    },
    div: {
        overflow: "hidden"
    },
    scroll : {
        overflow: "auto"
    },
    pdes: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-around",
    },
    button: {
        display: 'flex',
        justifyContent: "space-between",
    },
    tempLink: {
        '&:hover': {
            color: '#FFFFFF',
            textDecoration: 'none',
        },
    }
}))


function DashboardHook(props) {
    const classes = useStyles();

    return (

        <Grid container>

            <Grid item xs={0} sm={3}>
            </Grid>
            <Grid item container className={classes.paper} xs={12} sm={6} spacing={3}>
                <Typography variant="h5" className={classes.signup}>
                    Project Dashboard
                </Typography>

                <Button component={Link} to={'/addProject'} color="primary" variant="contained" className={classes.link}>
                    Add Project
                </Button>


                <Grid container xs={12}  className={classes.newpaper} spacing={4}>
                    <Divider variant="middle" className={classes.margin}/>

                    {props.projects.map(elem => (
                        <Grid item xs={12}>
                            <Card className={classes.root}>
                                <CardContent className={classes.content}>
                                    <Button color="primary" variant="outlined" className={classes.tag}>{elem.projectIdentifier}</Button>
                                    <div className={classes.pdes}>
                                        <Typography variant="h4">
                                            {elem.projectName}
                                        </Typography>
                                        <Typography variant="h6">
                                            {elem.description}
                                        </Typography>
                                    </div>
                                    <div className={classes.button}>
                                        <ButtonGroup
                                            orientation="vertical"
                                            aria-label="vertical outlined primary button group"
                                        >
                                            <Button component={Link} to={`/projectBoard/${elem.projectIdentifier}`} variant="contained" color="primary" className={classes.tempLink}>Project Board</Button>
                                            <Button component={Link} to={`/updateProject/${elem.projectIdentifier}`} variant="outlined" color="default" className={classes.tempLink}>Update Project Info</Button>
                                            <Button variant="contained" color="secondary" className={classes.tempLink} onClick={props.onDeleteClick.bind(this,elem.projectIdentifier)}>Delete Project</Button>
                                        </ButtonGroup>
                                    </div>
                                </CardContent>
                            </Card>

                        </Grid>


                    ))}
                </Grid>
            </Grid>
            <Grid item xs={0} sm={3}>
            </Grid>
        </Grid>
    );
}

export default DashboardHook;