import React, {Component} from 'react';
import {connect} from "react-redux";
import {getProjectTask, updateProjectTask} from "../../../actions/backlogActions";
import {Link} from "react-router-dom";
import * as classnames from "classnames";
import UpdateProjectTaskHook from "../../../hooks/UpdateProjectTaskHook";


class UpdateProjectTask extends Component {
    constructor() {
        super();

        this.state = {
            id: "",
            created_At: "",
            projectSequence: "",
            summary: "",
            acceptanceCriteria: null,
            status: "",
            priority: "",
            dueDate: null,
            projectIdentifier: "",
            errors: {}
        };

        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentDidMount() {
        const {backlog_id, pt_id} = this.props.match.params;
        this.props.getProjectTask(backlog_id, pt_id, this.props.history);
    }

    componentWillReceiveProps(nextProps) {

        if(nextProps.errors) {
            this.setState({errors: nextProps.errors});
        }
        const {
            id,
            created_At,
            projectSequence,
            summary,
            acceptanceCriteria,
            status,
            priority,
            dueDate,
            projectIdentifier} = nextProps.project_task;

        this.setState({
            id,
            created_At,
            projectSequence,
            summary,
            acceptanceCriteria,
            status,
            priority,
            dueDate,
            projectIdentifier
        });
    }

    onChange(e){
        this.setState({[e.target.name]: e.target.value});
    }

    onSubmit(e){
        e.preventDefault();

        const updateProjectTask = {
            id:this.state.id,
            created_At:this.state.created_At,
            projectSequence:this.state.projectSequence,
            summary:this.state.summary,
            acceptanceCriteria:this.state.acceptanceCriteria,
            status:this.state.status,
            priority:this.state.priority,
            dueDate:this.state.dueDate,
            projectIdentifier: this.state.projectIdentifier
        }

        this.props.updateProjectTask(this.state.projectIdentifier, this.state.projectSequence, updateProjectTask, this.props.history)
    }

    render() {
        return (
            <UpdateProjectTaskHook onSubmit={this.onSubmit} summary={this.state.summary} onChange={this.onChange} acceptanceCriteria={this.state.acceptanceCriteria} dueDate={this.state.dueDate} priority={this.state.priority} status={this.state.status}/>
        );
    }
}

const mapStateToProps = state => ({
    project_task: state.backlog.project_task,
    errors: state.errors
});

export default connect(mapStateToProps, {getProjectTask, updateProjectTask})(UpdateProjectTask);