import React, {Component} from 'react';
import {connect} from "react-redux";
import {logout} from "../../actions/securityActions";
import HeaderHookNotAuthenticated from "../../hooks/HeaderHookNotAuthenticated";
import HeaderHookAuthenticated from "../../hooks/HeaderHookAuthenticated";

class Header extends Component {

    constructor() {
        super();
        this.logout = this.logout.bind(this);
    }

    logout() {
        this.props.logout();
        window.location.href = "/";
    }

    render() {
        const {validToken , user } = this.props.security;
        if(validToken && user) {
            return (
                <HeaderHookAuthenticated user={user.fullname} logout={this.logout}/>
                );
        } else {
            return (
                <HeaderHookNotAuthenticated />
            );
        }
    }
}

const mapStateToProps = state => ({
    security: state.security
})

export default connect(mapStateToProps, {logout})(Header);