import React, {Component} from 'react';
import * as classnames from "classnames";
import {connect} from "react-redux";
import {createProject, getProject} from "../../actions/projectAction";
import AddProjectHook from "../../hooks/AddProjectHook";
import UpdateProjectHook from "../../hooks/UpdateProjectHook";

class UpdateProject extends Component {

    constructor() {
        super();
        this.state = {
            id: "",
            projectName: "",
            projectIdentifier: "",
            description: "",
            start_date: "",
            end_date: "",
            errors: {}
        };

        this.onSubmit = this.onSubmit.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    componentDidMount() {
        const {id} = this.props.match.params;
        this.props.getProject(id, this.props.history);
    }

    onChange(e) {
        this.setState({[e.target.name]: e.target.value});
    }

    onSubmit(e) {
        e.preventDefault();
        const updateProject = {
            id: this.state.id,
            projectName: this.state.projectName,
            projectIdentifier: this.state.projectIdentifier,
            description: this.state.description,
            start_date: this.state.start_date,
            end_date: this.state.end_date
        }
        this.props.createProject(updateProject, this.props.history);
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors) {
            this.setState({errors: nextProps.errors});
        }
        const {
            id,
            projectName,
            projectIdentifier,
            description,
            start_date,
            end_date
        } = nextProps.project;

        this.setState({
            id,
            projectName,
            projectIdentifier,
            description,
            start_date,
            end_date
        })
    }



    render() {
        return (
            <UpdateProjectHook onSubmit={this.onSubmit} projectName={this.state.projectName} onChange={this.onChange} projectIdentifier={this.state.projectIdentifier} description={this.state.description} start_date={this.state.start_date} end_date={this.state.end_date}/>
        );
    }
}

const mapStateToProps = state => ({
    project: state.project.project,
    errors: state.errors
})

export default connect(mapStateToProps,{getProject , createProject})(UpdateProject);