import React, {Component} from 'react';
import {connect} from "react-redux";
import {deleteProjectTask} from "../../../actions/backlogActions";
import ProjectTaskHook from "../../../hooks/ProjectTaskHook";

class ProjectTask extends Component {

    onDeleteClick = (backlog_id, pt_id) => {
        this.props.deleteProjectTask(backlog_id,pt_id , this.props.history);
    }

    render() {
        const {project_task} = this.props;
        return (
            <ProjectTaskHook project_task={project_task} onDeleteClick={this.onDeleteClick} history={this.props.history}/>
        );
    }
}

export default connect(null,{deleteProjectTask})(ProjectTask);