import React, {Component} from 'react';
import {connect} from "react-redux";
import {deleteProject, getProjects} from "../actions/projectAction";
import DashboardHook from "../hooks/DashBoardHook";

class DashBoard extends Component {

    componentDidMount() {
        this.props.getProjects();
    }

    onDeleteClick = id => {
        this.props.deleteProject(id);
    }

    render() {

        const {projects} = this.props.project;

        return (
            <DashboardHook projects={projects} onDeleteClick={this.onDeleteClick}/>
        );
    }
}

const mapStateToProps = state =>  ({
    project: state.project
})

export default connect(mapStateToProps,{getProjects , deleteProject})(DashBoard);