import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import ListAltIcon from '@material-ui/icons/ListAlt';

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(10),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(2),
        backgroundColor: theme.palette.primary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(4, 0, 4),
    },
    signup: {
        fontSize: theme.spacing(5.4)
    },
}));

function AddProjectTaskHook(props) {
    const classes = useStyles();
    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper} >
                <Avatar className={classes.avatar}>
                    <ListAltIcon />
                </Avatar>
                <Typography variant="h5" className={classes.signup}>
                    Add Project Task
                </Typography>
                <form className={classes.form} noValidate onSubmit={props.onSubmit}>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <TextField
                                name="summary"
                                variant="outlined"
                                required
                                fullWidth
                                id="summary"
                                label="Summary"
                                autoFocus
                                onChange={props.onChange}
                                value={props.summary}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                name="acceptanceCriteria"
                                variant="outlined"
                                fullWidth
                                id="acceptanceCriteria"
                                label="Acceptance Criteria"
                                multiline
                                rowsMax={3}
                                onChange={props.onChange}
                                value={props.acceptanceCriteria}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="dueDate"
                                name="dueDate"
                                label="Due Date"
                                type="date"
                                defaultValue="yyyy-mm-dd"
                                fullWidth
                                required
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={props.onChange}
                                value={props.dueDate}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="priority"
                                select
                                label="Select Priority"
                                fullWidth
                                SelectProps={{
                                    native: true,
                                }}
                                helperText="Please select your priority"
                                onChange={props.onChange}
                                name="priority"
                                value={props.priority}>
                                <option key="1" value={1}>HIGH</option>
                                <option key="2" value={2}>MEDIUM</option>
                                <option key="3" value={3}>LOW</option>
                            </TextField>
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="status"
                                select
                                label="Select Status"
                                fullWidth
                                SelectProps={{
                                    native: true,
                                }}
                                helperText="Please select the task status"
                                onChange={props.onChange}
                                name="status"
                                value={props.status}>
                                <option key="1" value="TO_DO">TO DO</option>
                                <option key="2" value="IN_PROGRESS">IN PROGRESS</option>
                                <option key="3" value="DONE">DONE</option>
                            </TextField>
                        </Grid>
                    </Grid>
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Submit
                    </Button>
                </form>
            </div>
        </Container>
    );
}

export default AddProjectTaskHook;