import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import ListAltIcon from '@material-ui/icons/ListAlt';

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(10),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(2),
        backgroundColor: theme.palette.primary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(4, 0, 4),
    },
    signup: {
        fontSize: theme.spacing(5.4)
    },
}));

function AddProjectHook(props) {
    const classes = useStyles();
    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper} >
                <Avatar className={classes.avatar}>
                    <ListAltIcon />
                </Avatar>
                <Typography variant="h5" className={classes.signup}>
                    Project Form
                </Typography>
                <form className={classes.form} noValidate onSubmit={props.onSubmit}>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <TextField
                                name="projectName"
                                variant="outlined"
                                required
                                fullWidth
                                id="projectName"
                                label="Project Name"
                                autoFocus
                                value={props.projectName}
                                onChange={props.onChange}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                name="projectIdentifier"
                                variant="outlined"
                                required
                                fullWidth
                                id="projectIdentifier"
                                label="Unique Project ID"
                                value={props.projectIdentifier}
                                onChange={props.onChange}

                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                name="description"
                                variant="outlined"
                                fullWidth
                                id="description"
                                label="Project Description"
                                onChange={props.onChange}
                                multiline
                                rowsMax={3}
                                value={props.description}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="start_date"
                                name="start_date"
                                label="Start Date"
                                type="date"
                                defaultValue="yyyy-mm-dd"
                                fullWidth
                                required
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={props.onChange}
                                value={props.start_date}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                id="end_date"
                                name="end_date"
                                label="Estimated End Date"
                                type="date"
                                defaultValue="yyyy-mm-dd"
                                fullWidth
                                required
                                InputLabelProps={{
                                    shrink: true,
                                }}
                                onChange={props.onChange}
                                value={props.end_date}
                            />
                        </Grid>
                    </Grid>
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Submit
                    </Button>
                </form>
            </div>
        </Container>
    );
}

export default AddProjectHook;