import React, {Component} from 'react';
import {connect} from "react-redux";
import {createProject} from "../../actions/projectAction";
import AddProjectHook from "../../hooks/AddProjectHook";
import secureRoute from "../../securityUtils/secureRoute";

class AddProject extends Component {
    constructor() {
        super();

        this.state = {
            projectName: "",
            projectIdentifier: "",
            description: "",
            start_date: "",
            end_date: "",
            errors:{}
        };
        this.onSubmit = this.onSubmit.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    onChange(e) {
        this.setState({ [e.target.name]: e.target.value });
    }

    onSubmit(e) {
        e.preventDefault();
        const newProject = {
            projectName: this.state.projectName,
            projectIdentifier: this.state.projectIdentifier,
            description: this.state.description,
            start_date: this.state.start_date,
            end_date: this.state.end_date
        }
        this.props.createProject(newProject,this.props.history);
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.errors){
            this.setState({errors:nextProps.errors});
        }
    }


    render() {
        return (
            <AddProjectHook onSubmit={this.onSubmit} projectName={this.state.projectName} onChange={this.onChange} projectIdentifier={this.state.projectIdentifier} description={this.state.description} start_date={this.state.start_date} end_date={this.state.end_date}/>
        );
    }
}

const mapStateToProps = state => ({
    errors: state.errors
})

export default connect(mapStateToProps,{createProject})(AddProject);