import React, {Component} from 'react';
import {connect} from "react-redux";
import {addProjectTask} from "../../../actions/backlogActions";
import AddProjectTaskHook from "../../../hooks/AddProjectTaskHook";

class AddProjectTask extends Component {

    constructor(props) {
        super(props)
        const {id} = this.props.match.params;
        this.state = {
            summary: "",
            acceptanceCriteria: "",
            status: "",
            priority: 3,
            dueDate: "",
            projectIdentifier: id,
            errors: {}
        };
        this.onChange = this.onChange.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({errors: nextProps.errors});
        }
    }

    onChange(e) {
        this.setState({[e.target.name]:e.target.value})
    }

    onSubmit(e){
        e.preventDefault();
        const newTask = {
            summary: this.state.summary,
            acceptanceCriteria: this.state.acceptanceCriteria,
            status: this.state.status,
            priority: this.state.priority,
            dueDate: this.state.dueDate
        }
        this.props.addProjectTask(this.state.projectIdentifier,newTask,this.props.history);
    }

    render() {

        return (
            <AddProjectTaskHook onSubmit={this.onSubmit} summary={this.state.summary} onChange={this.onChange} acceptanceCriteria={this.state.acceptanceCriteria} dueDate={this.state.dueDate} priority={this.state.priority} status={this.state.status}/>
        );
    }
}

const mapStateToProps = state => ({
    errors: state.errors
});

export default connect(mapStateToProps,{addProjectTask})(AddProjectTask);